textarea {
  border: 0;
  width: 100%;
  font-size: 1em;
  line-height: 1.5;
}

.resources .content-stripe > h2 {
  display: inline-block;
}

.edit-content {
  color: #afafaf;
  cursor: default;
  background-color: #e1e1e1;
  border-radius: 8px;
  padding: 5px 10px;
}

.resource-info input {
  width: 50%;
  display: inline-block;
}

.resource-notes textarea {
  height: fit-content;
  display: block;
}

.add-new-feed {
  flex-direction: column;
  width: 75%;
  margin: 100px auto;
  display: flex;
}

.new-feed-title {
  grid-template-columns: 1fr auto auto;
  align-items: baseline;
  gap: 5px;
  display: grid;
}

.new-feed-title #title {
  font-weight: 700;
}

.uploaded-media {
  flex-wrap: wrap;
  gap: 5px;
  margin-block: 15px;
  line-height: 0;
  display: flex;
}

.uploaded-media li {
  border: 1px solid #b5b5b5;
  width: 100px;
  height: 100px;
}

.uploaded-media li:hover {
  border-color: #000;
  position: relative;
}

.uploaded-media li img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.uploaded-media li.video img {
  border-block: 6px dashed #000;
}

.uploaded-media li:not(:last-of-type):hover:after {
  content: "âœ—";
  color: #919191ed;
  cursor: pointer;
  background-color: #e1e1e1b8;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  font-size: 50px;
  font-weight: 700;
  display: flex;
  position: absolute;
  top: 0;
}

.add-media-button {
  cursor: pointer;
  justify-content: center;
  height: 100%;
  display: flex;
}

.add-media-button span {
  align-self: center;
  font-size: 80px;
  font-weight: 100;
  position: relative;
  top: -6px;
}

.submit-new-feed {
  border: 1px solid;
  align-self: flex-end;
  padding: 10px 30px;
}
